
#vux_view_box_body {
  background: #EEEEEE;
}
#orderList {
  background: #EEEEEE;
  height: 100%;
}
#orderList .order_type_wrap {
  line-height: 38px;
}
#orderList .order_type_wrap .order_type.cur {
  color: #7EC828;
  border-bottom: 2px solid #7EC828;
}
#orderList .order_type_wrap .order_type {
  display: inline-block;
  width: 23.9%;
  border-bottom: 2px solid #ccc;
  text-align: center;
  font-size: 14px;
  color: #000;
}
#orderList .noprd div {
  margin: 0 auto;
  width: 50%;
}
#orderList .noprd p {
  text-align: center;
  line-height: 50px;
}
#orderList .noprd a {
  background-color: #7EC828;
  color: #fff;
  line-height: 36px;
  height: 36px;
  font-size: 14px;
  text-align: center;
  display: block;
  width: 50%;
  margin: 0 auto;
}



