
#allproduct {
  background: #fff;
  height: 100%;
  overflow: hidden;
}
#allproduct .titleList {
  float: left;
  width: 30%;
  height: 100%;
  background: #F8F8F8;
}
#allproduct .titleList ul li {
  font-size: 70%;
  padding: 10px 0 10px 15px;
  border-left: 2px solid transparent;
}
#allproduct .titleList ul li.active {
  background: #fff;
  border-left: 2px solid #82C92F;
}
#allproduct .proList {
  float: left;
  width: 70%;
}
#allproduct .proList .sort_box ul {
  overflow: hidden;
  font-size: 75%;
  width: 100%;
}
#allproduct .proList .sort_box ul li {
  height: 45px;
  line-height: 25px;
  float: left;
  width: 20%;
  padding: 10px 0;
  text-align: center;
}
#allproduct .proList .sort_box ul li b {
  display: inline-block;
  vertical-align: middle;
  width: 7px;
  height: 15px;
  margin-left: 5px;
  background: url(../assets/images/icon.png) no-repeat 0px 0px;
  background-size: 100%;
}
#allproduct .proList .sort_box ul li b.up {
  background: url(../assets/images/icon.png) no-repeat 0px -14px;
  background-size: 100%;
}
#allproduct .proList .sort_box ul li b.down {
  background: url(../assets/images/icon.png) no-repeat 0px -28px;
  background-size: 100%;
}
#allproduct .proList .sort_box ul li span {
  vertical-align: middle;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 5px;
  background: url(../assets/images/stock.png) no-repeat center;
  background-size: 100%;
}
#allproduct .proList .sort_box ul li.green {
  color: #74B72C;
}
#allproduct .proList .sort_box ul li.green span {
  background: url(../assets/images/hasstock.png) no-repeat center;
  background-size: 100%;
}
#allproduct .proList .sort_box ul li:last-child {
  width: 40%;
}
#allproduct .proList .main_box {
  height: 420px;
  overflow: scroll;
  padding-bottom: 40px;
}
#allproduct .proList .main_box ul li {
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
  margin-left: 15px;
  overflow: hidden;
}
#allproduct .proList .main_box ul li .img_box {
  width: 96px;
  height: 96px;
  float: left;
}
#allproduct .proList .main_box ul li .img_box img {
  width: 100%;
}
#allproduct .proList .main_box ul li .instr h5 {
  height: 50px;
  font-size: 80%;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 10px;
  color: #333;
}
#allproduct .proList .main_box ul li .instr span {
  font-size: 80%;
  color: #e4393c;
}
#allproduct .proList .main_box div.button_box {
  padding: 20px 30px;
  width: 100%;
  text-align: center;
}
#allproduct .proList .main_box div.button_box button {
  border: 1px solid #ccc;
  background: #fff;
  color: #666666;
  width: 100%;
  height: 30px;
  border-radius: 5px;
}



