
#productInfo {
  background: #F5F5F5;
}
#productInfo .margin_bottom {
  margin-bottom: 10px;
}
#productInfo .vux-no-group-title {
  margin-top: 0;
  font-size: 80%;
}
#productInfo .weui-cell__hd {
  margin-right: 40px;
  font-size: 80%;
}
#productInfo .weui-cell__hd label {
  width: 60px;
}
#productInfo .weui-cell {
  padding: 20px 10px;
}
#productInfo .vux-slider > .vux-swiper > .vux-swiper-item > a > .vux-swiper-desc {
  display: none;
}
#productInfo .vux-slider > .vux-indicator > a,
#productInfo .vux-slider .vux-indicator-right > a {
  margin-left: 10px;
}
#productInfo .vux-slider > .vux-indicator > a > .vux-icon-dot,
#productInfo .vux-slider .vux-indicator-right > a > .vux-icon-dot {
  width: 8px;
  height: 8px;
  border-radius: 4px;
}
#productInfo .addCart_box {
  background: #fff;
  z-index: 1000;
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  height: 50px;
  line-height: 50px;
  overflow: hidden;
}
#productInfo .addCart_box > div.button_box {
  float: right;
  width: 80%;
  background: #FF8301;
  text-align: center;
  color: #fff;
}
#productInfo .addCart_box > div.img_box {
  float: left;
  width: 30px;
  height: 30px;
  margin: 10px 10px 0 20px;
  background: #Fff url(../assets/images/car_bg.png) no-repeat;
  background-size: 100%;
}
#productInfo .silder_box {
  padding: 10px 0px;
  background: #FFF;
  border-bottom: 1px solid #E5E5E5;
}
#productInfo .proInfo {
  font-size: 80%;
  padding-bottom: 10px;
}
#productInfo .proInfo .like_button {
  background: #FFF;
  padding: 10px;
  float: right;
  text-align: right;
  font-size: 1.2rem;
}
#productInfo .proInfo .like_button span {
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  margin: .2rem .5rem;
  background: url(../assets/images/like.png) no-repeat 0rem 0rem;
  background-size: 100%;
  vertical-align: middle;
}
#productInfo .proInfo .like_button span.active {
  background: url(../assets/images/like.png) no-repeat 0px -24px;
  background-size: 100%;
}
#productInfo .proInfo .info-title {
  padding: 5px 10px;
  background: #FFF;
  color: #333;
  line-height: 1.5rem;
}
#productInfo .proInfo .info-feihua {
  padding: 3px 10px;
  background: #FFF;
  color: #ff5a00;
  font-size: 75%;
  display: block;
  line-height: 1.3rem;
  word-wrap: break-word;
}
#productInfo .proInfo .info-feihua a {
  color: #ff5a00;
  text-decoration: underline;
}
#productInfo .proInfo .info-price {
  padding: 3px 10px;
  background: #FFF;
  color: #F6382C;
  line-height: 2.0rem;
  border-bottom: 1px solid #E5E5E5;
}
#productInfo .select_kinds {
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
  background: #fff;
  padding: 0 10px;
}
#productInfo .select_kinds .select_box {
  overflow: hidden;
  padding-tOP: 10px;
  border-bottom: 1px solid #e5e5e5;
}
#productInfo .select_kinds .select_box span {
  float: left;
  font-size: 80%;
  margin: 0 0px 5px 0px;
  padding: 8px 0px;
}
#productInfo .select_kinds .select_box ul {
  margin-left: 50px;
}
#productInfo .select_kinds .select_box ul li {
  float: left;
  font-size: 80%;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  margin: 0 5px 10px 5px;
  padding: 8px 16px;
}
#productInfo .select_kinds .select_box ul li.active {
  color: #FD532D;
  border: 1px solid #FD532D;
}
#productInfo .select_kinds .select_box:last-child {
  border-bottom: none;
}
#productInfo .num_box {
  padding: 0 10px;
  background: #fff;
  overflow: hidden;
  font-size: 80%;
}
#productInfo .num_box > span {
  float: left;
  padding: 10px 0;
  height: 30px;
  line-height: 30px;
}
#productInfo .num_box > div {
  float: right;
  padding: 10px;
}
#productInfo .num_box > div span {
  height: 30px;
  line-height: 30px;
  display: inline-block;
  text-align: center;
}
#productInfo .num_box > div span:nth-child(2n+1) {
  width: 30px;
  border: 1px solid #eee;
  background: #F6F6F6;
  border-radius: 5px 0 0 5px;
}
#productInfo .num_box > div span:nth-child(3) {
  border-radius: 0px 5px 5px 0px;
}
#productInfo .num_box > div span.disabled {
  background: #B6B6B6;
}
#productInfo .num_box > div span:nth-child(2n) {
  border-top: 1px solid #eee;
  width: 40px;
  margin-left: -5px;
  margin-right: -5px;
  border-bottom: 1px solid #eee;
}
#productInfo .info_main {
  background: #fff;
}
#productInfo .info_main .info_title {
  padding: 10px;
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
}
#productInfo .info_main .info_title ul {
  overflow: hidden;
}
#productInfo .info_main .info_title ul li {
  float: left;
  padding: 5px 10px;
  font-size: 80%;
  width: 33%;
  text-align: center;
}
#productInfo .info_main .info_title ul li:nth-child(2) {
  border-left: 1px solid #e5e5e5;
  border-right: 1px solid #e5e5e5;
}
#productInfo .info_main .info_title ul li.active {
  color: #80CB19;
}
#productInfo .info_main .info_img {
  padding-bottom: 15px;
  padding-top: 15px;
}
#productInfo .info_main .info_img li {
  text-align: center;
}
#productInfo .info_main .info_img li img {
  width: 93%;
}



